@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
    margin: 0;
    padding: 0;
}
body {
    width: 100%;
    height: 100vh;
    background-color: rgba(101, 199, 255, 0.301);
    background-image: url(/pocasi/static/media/clouds1.9015724e.png);
    /* background-repeat: no-repeat; */
    background-size: 200px;
    /* background-position: right bottom; */
    
}


.formWrapper {
    width: 100%;
    height: 100%;
    
}

.form {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 5px solid black;
    width: 100%;
    height: 250px;
    box-sizing: border-box;
    padding: 40px;
    font-family: 'Open Sans';
    background-color: #fff;
}

.cityInput {
    width: 300px;
    height: 60px;
    font-size: 22px;
    border: none;
    border-bottom: 2px solid black;
    padding: 0;
    box-shadow: none;
    box-sizing: border-box;
    font-family: 'Open Sans';
    text-align: center;
    margin-top: 15px;
}

.submitButton {
    border: 2px solid black;
    width: 100px;
    height: 40px;
    box-shadow: none;
    background-color: #fff;
    cursor: pointer;
    margin-top: 19px;
    font-family: 'Open Sans';
    border-radius: 8px;
}

.submitButton:hover {
    background-color: black;
    color: white;
}

.submitButton:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.linkDiv {
    display: flex;
    justify-content: center;    
    width: 100%;
    box-shadow: none;
    text-align: center;
    z-index: -1;
}

 .linkDiv a {
    color: black;
    font-weight: bold;
    -webkit-text-decoration-style: none;
            text-decoration-style: none;
    text-decoration: none;
    font-size: 36px;
}

.linkDiv div {
    width: 250px;
    height: 100px;
    line-height: 100px;
    padding: 10px;
    border-radius: 8px;
    border: 2px solid rgb(190, 186, 186);
    margin: 5px;
    background-color: white;
    margin-top: 40px;
    font-family: 'Open Sans';
}

.linkDiv div p {
    display: inline;
    color: tomato;
}

.historyDiv {
    width: 100%;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 0 auto;
    z-index: 10;
    padding-top: 35px;
    font-family: 'Open Sans';
}

.historyDiv h3 {
    margin: 0;
}

.linksWrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    z-index: 10;
    padding-top: 35px;
}

.historyLinkDiv {
    width: 100px;
    height: 50px;
    line-height: 42px;
    padding: 10px;
    border-radius: 8px;
    border: 2px solid rgb(190, 186, 186);
    margin: 5px;
    background-color: white;
    font-size: 20px;
}

.historyLinkDiv a {
    width: 120px;
    color: rgb(78, 78, 78);
    font-size: 16px;
}


.ForecastDayButton {
    width: 60px;
    height: 60px;
    background-color: #ffffff;
    border: 2px solid rgb(190, 186, 186);
    color: rgb(88, 87, 87);
    text-align: center;
    border-radius: 50%;
    font-weight: 400;
    font-size: 14px;
    margin: 10px;
    cursor: pointer;
    font-family: 'Open Sans';
}

.ForecastDayButton:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.moreDays {
    grid-row: 1 / 3;
    flex-direction: column;
    display: none;
    padding-top: 5px;
    
}


.clicked{
    display: flex;
}



.loading {
    text-align: center;
    margin-top: 150px;
}

.detailWrapper {
    width: 100%;
    min-height: 100vh;
    background-color: rgb(255, 255, 177);;
    font-family: 'Open Sans';
}



.cityWrapper {
    width: 100%;
    height: 250px;
    background-color: #fff;
}

.cityWrapper h3 {
    text-align: center;
    margin: 0;
}

.cityName {
    margin: 0;
    width: 100%;
    height: 130px;
    text-align: center;
    line-height: 130px; 
    font-size: 70px;
    background-color: #fff;
   
}

.sunny   {
    -webkit-animation: cityNameRotate 10s infinite linear;
            animation: cityNameRotate 10s infinite linear;
}

@-webkit-keyframes cityNameRotate {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes cityNameRotate {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);

}


.showForecast {
    width: 60px;
    height: 60px;
    background-color: #ffffff;
    border: 2px solid rgb(190, 186, 186);
    color: rgb(88, 87, 87);
    text-align: center;
    border-radius: 50%;
    font-weight: 400;
    font-size: 14px;
    margin: 10px;
    cursor: pointer;
    font-family: 'Open Sans';
    margin-top: 10px;
}

.showForecast:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.tempWrapper, .windWrapper {
    width: 100%;
    padding: 20px;
    padding-left: 0;
    padding-right: 110px;
    box-sizing: border-box;
    grid-column: 2 / -1;
    justify-self: center;
    display: flex;
    flex-direction: row;
    flex-wrap:wrap;
    justify-content: center;
}

.windWrapper {
    margin-top: -90px;
}

.tempWrapper > span {
    font-size: 32px;
    font-weight: bold;
}

.tempDiv, .tempMinDiv, .tempMaxDiv, .windSpeedDiv, .windDirDiv {
    display: flex;
    flex-direction: column;
    width: 150px;
    height: 190px;
    padding: 10px;
    border-radius: 8px;
    border: 2px solid rgb(190, 186, 186);
    margin: 5px;
    background-color: white;
    font-size: 20px;
    /* box-shadow: 1px 1px rgb(102, 102, 102); */
}

.tempDiv span:first-child {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
}
.tempMinDiv span:first-child {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
}
.tempMaxDiv span:first-child {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
}
.windDirDiv span:first-child {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
}
.windSpeedDiv span:first-child {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
}


.goBack {
    width: 30px;
    height: 30px;
    box-shadow: none;
    background-color: #fff;
    border: 2px solid black;
    border-radius: 50%;
    font-weight: bold;
    margin-left: 15px;
    margin-top: 15px;
    cursor: pointer;
}

.goBack:hover {
    background-color: black;
    color: white;
}

.goBack:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}




@media only screen and (max-width: 600px) {
    .tempDiv, .tempMinDiv, .tempMaxDiv, .windSpeedDiv, .windDirDiv {
        height: 120px;
    }

    .windWrapper {
        margin-top: -20px;
        padding-top: 0;
    }

    .cityName {
        font-size: 55px;        
    }

    .cityWrapper h3 {
        font-size: 16px;    
    }

}

@media only screen and (max-width: 800px) {
    .tempWrapper, .windWrapper {
        padding-right: 20px;
    }
}
